* {
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}
body {
  font-family: "Times New Roman", Times, serif;
}
.nav-area {
  height: 42px;
  width: 100%;
}

.nav-area ul {
  float: right;
  margin-right: 20px;
}
.nav-area ul li {
  display: inline-block;
  line-height: 30px;
  margin: 0 5px;
}
.nav-area ul li a {
  color: #9831cc;
  font-size: 14px;
  padding: 5px 5px;
  text-transform: uppercase;
  font-weight: bold;
  text-decoration: none;
}

.btn-area {
  font-size: 30px;
  color: #1b58b7;
  float: right;
  line-height: 62px;
  margin-right: 40px;
  cursor: pointer;
  display: none;
}
#box {
  display: none;
}
@media (max-width: 860px) {
  .btn-area {
    display: block;
    color: #9831cc;
  }
  .nav-area ul {
    position: absolute;
    width: 55%;
    height: 60vh;
    background: lavender;
    top: 90px;
    left: -100%;
    text-align: center;
    transition: all 0.5s;
    z-index: 1;
    border-radius: 5px;
  }
  .nav-area ul li {
    display: block;
    margin: 0;
    line-height: 20px;
    padding: 4px 0;
  }
  .nav-area ul li a {
    font-size: 16px;
  }
  .nav-area ul li a:hover {
    background: #1b58b7;
    color: #fff !important;
    text-decoration: none;
  }

  #box:checked ~ ul {
    left: 0;
  }
  .nav-link:hover {
    background: #1b58b7;
    color: #fff !important;
    text-decoration: none;
  }
}

